.nav-div{
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 1020;
    background-color: white;
}
nav{
    padding: 5px;
    margin: 0 50px;
    border: 1px solid black;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
ul{
    display: flex;
    margin: 0;
    border: 1px solid red;
}
li{
    list-style: none;
    margin: 2px;
    padding: 2px;
}